import { useEffect  } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';


const Rotas = () => {

     
const location = useLocation()
const navigate = useNavigate()


useEffect(() => {
    
        if( location.state.CODIGO_LOCALIDADE === 1) { navigate('/salaPreparo/listagem',         { state: { CODIGO_LOCALIDADE: 1,        CODIGO_SUBLOCALIDADE: location.state.CODIGO_SUBLOCALIDADE    }}) }
        if( location.state.CODIGO_LOCALIDADE === 2) { navigate('/portaria/listagem',            { state: { CODIGO_LOCALIDADE: 2,        CODIGO_SUBLOCALIDADE: location.state.CODIGO_SUBLOCALIDADE    }}) }
        if( location.state.CODIGO_LOCALIDADE === 3) { navigate('/inducao/listagem',             { state: { CODIGO_LOCALIDADE: 3,        CODIGO_SUBLOCALIDADE: location.state.CODIGO_SUBLOCALIDADE, SALA: location.state.SUBLOCALIDADE    }}) }
        if( location.state.CODIGO_LOCALIDADE === 4) { navigate('/salaCirurgica/preparoLimpeza', { state: { CODIGO_LOCALIDADE: 4,        CODIGO_SUBLOCALIDADE: location.state.CODIGO_SUBLOCALIDADE    }}) }
        if( location.state.CODIGO_LOCALIDADE === 5) { navigate('/rpa/listagem',                 { state: { CODIGO_LOCALIDADE: 5,        CODIGO_SUBLOCALIDADE: location.state.CODIGO_SUBLOCALIDADE    }}) }


}, [location, navigate]);
    
};

export default Rotas;